import React, { Component } from "react";
import Header from "./Header";
import axios from "axios";
import Articles from "./Articles";
import "./App.css";

class App extends Component {
  constructor() {
    super();

    this.state = {
      isLoggedIn: false,
      articles: [],
      user: {},
      isLoggedIn: false,
    };
  }

  // on mount /refresh   - make a check call to the server for login status and details

  componentDidMount = () => {
    axios({
      url: "/display",
      method: "GET",
      responseType: "json",
      mode: "no-cors",
      headers: {
        "Access-Control-Allow-Origin": "*",
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      withCredentials: true,
      credentials: "same-origin",
      crossdomain: true,
    })
      .then((response) => {
        this.setState({
          user: response.data
        }, () => console.log(this.state.user))
      })
      .catch((error) => {
        if (typeof error.response === "undefined") {
          this.setState({
            isLoggedIn: false,
          }, () => console.clear());
        }
      });
  };

  // make function to recieve the articles data
  getArticles = () => {
    axios({
      url: "/articles",
      method: "GET",
      responseType: "json",
      mode: "no-cors",
      headers: {
        "Access-Control-Allow-Origin": "*",
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      withCredentials: true,
      credentials: "same-origin",
      crossdomain: true,
    }).then((response) => {
      const articleResponse = response.data;

      // set state to the response from the API
      this.setState({
        articles: articleResponse,
      });
    });
  };

  render() {
    return (
      <div>
        <Header
          loggedIn={this.state.openLogin}
          getArticlesFunc={this.getArticles}
        />
        <Articles
          getArticlesFunc={this.getArticles}
          articleData={this.state.articles}
        />
      </div>
    );
  }
}

export default App;
